import PropTypes from "prop-types"
import React from "react"
import styled from 'styled-components'


const Project = ({ title, gif, info, technologies, link, index }) => {

  const Container = styled.div`
  width: 100%;
  padding-right: ${props => props.index % 2 === 0 ? "60%" : "0%"};
  padding-left: ${props => props.index % 2 === 0 ? "0%" : "60%"};
  margin-bottom: -10em;
  height: 400px;
  @media (max-width: 700px) {
    height: 100%;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 0;
  }
  `
  const TitleContainer = styled.div``

  const GifContainer = styled.div``

  const InfoContainer = styled.div``

  return (
    <Container index={index}>
      <TitleContainer>title: {title}</TitleContainer>
      <GifContainer>
        img:
        <img src={gif} alt="gif"></img>
      </GifContainer>
      <InfoContainer>info: {info}</InfoContainer>
    </Container>
  )
}

Project.propTypes = {
  siteTitle: PropTypes.string,
}

Project.defaultProps = {
  siteTitle: ``,
}

export default Project
